body{
  margin: 0;
  padding: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.title{
  margin-top: 15px;
  text-align: center;
  border: 5px solid black;
  font-size: 25px;
  background-color: rgb(0, 0, 0);
  color: white;
}

.title:hover{
  opacity: 0.8;
  box-shadow: 10px 10px 5px black;
}

.search_form{
  float: left;
  margin-left: 60px;
  border-radius: 5px;
  padding: 20px;
  font-weight: 900;
  height: 500px;
  width: 50%;
}

.result{
  float: right;
  margin-right: 60px;
  background-color: rgb(136, 156, 131);
  text-align: center;
  height: 500px;
  border-radius: 5px;
  padding: 20px;
  width: 50%;
}

.result_text{
  background-color: rgb(184, 162, 147);
  width: 50%;
  margin-top: 200px;
  margin: auto;
}

.result_text:hover{
  background-color: rgb(13, 26, 34);
  width: 50%;
  margin-top: 200px;
  margin: auto;
  color: #fff;
  zoom: 1.3;
}



.form_button {
  display: inline-block;
  border-radius: 4px;
  background-color: #000000;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 28px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  
}

.form_button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.form_button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.form_button:hover span {
  padding-right: 25px;
}

.form_button:hover span:after {
  opacity: 1;
  right: 0;
}

.city, .country {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.weather{
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

li{
  list-style-type: none;
}

.input1{
  margin-top: 20px;
}
.location-input{
  width: 250px;
  box-sizing: border-box;
  border: 2px solid rgb(26, 80, 9);
  border-radius: 4px;
  font-size: 16px;
  background-color: white; 
  background-repeat: no-repeat;
  margin-left: 150px;
  padding: 12px 20px 12px 40px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}
.location-input:focus{
  width: 80%;
}

.main-display{
  margin-left: 40px;
  margin-right: 40px;
  display: flex;
  justify-content: space-around;
}

.selection-panel{
  flex: 1;
  display: flex;
  justify-content: center;

}



.weather-information{
  flex: 1;
  text-align: center;
  
}

.location{
  margin-top: 20px;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
  animation: 2s linear infinite location;
}

@keyframes location{
  0% {
    visibility: hidden;
    color: rgb(37, 9, 63);
}
50% {
    visibility: visible;
    color: rgb(20, 26, 82);
}
100% {
    visibility: visible;
    color: rgb(51, 74, 139);
}
}

.time{
  text-align: center;
}

.navigation button{
  font-size: 20px;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  background-color: rgb(25, 37, 22);
  box-shadow: 5px 10px 8px #888888;
}

.navigation{
  text-align: center;
}

.list{
  color: rgb(224,224,224);
}

.weather_data_days{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.days_data{
  width: 200px;
  height: 400px;
  margin: 20px 20px 20px 20px;
  text-align: center;
  color: rgb(255, 251, 0);
  background-image: linear-gradient(#2c3e50,#fd746c);
  transition: height 2s;
}

.days_data:hover{
  height: 500px;
}